body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.tab-scroll {
  height: 935px;
  overflow-y: auto;
}
body {
  background-color: black;
  background: url(https://i.imgur.com/SuzVQJs.jpg) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  min-height: 100vh;
  height: 100;
}
.btn {
  border: 0px solid transparent;
  border-radius: 0px;
  padding: 0.375rem 0.5rem;
  font-size: 1rem;
}
.btn-thin {
  /*background-color: #ddd;*/
  font-weight: 500;
  font-size: 1.25rem;
}
.gray {
  filter: grayscale(80%);
}
.xp-raise {
  position: absolute;
  bottom: 0px;
  right: 0px;
}
.txt-price {
  position: absolute;
  color: gold;
  bottom: 2px;
  left: 2px;
  margin-bottom: 0;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.nat-flag {
  position: absolute;
  top: 0px;
  right: 0px;
  height: 20px;
}
.txt-avail {
  position: absolute;
  color: white;
  bottom: 2px;
  left: 50%;
  margin-bottom: 0;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
p,
tr {
  font-size: 0.8rem;
  margin: 0.2rem;
}
.table-unit {
  height: 30px;
}
.table-flag {
  height: 30px;
  padding: 0.25rem;
}
.table-sm th,
.table-sm td {
  padding: 0rem;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media (min-width: 768px) {
  .popup-background {
    position: "absolute";
    left: "0%";
    top: "0%";
    width: "100%";
    height: "100%";
    opacity: "0.7";
    background-color: "#000";
  }
  .popup-simple {
    position: "absolute";
    left: "30%";
    top: "30%";
    width: "40%";
    height: "40%";
  }
  .popup-unit {
    position: absolute;
    right: 0%;
    width: 45%;
    z-index: 2;
  }
  .popup-unit-panel {
    position: absolute;
    left: 0%;
    width: 100%;
    z-index: 1;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .popup-background {
    position: "absolute";
    left: "0%";
    top: "0%";
    width: "100%";
    height: "100%";
    opacity: "0.7";
    background-color: "#000";
  }
  .popup-simple {
    position: "absolute";
    left: "30%";
    top: "30%";
    width: "40%";
    height: "40%";
  }
  .popup-unit {
    position: absolute;
    right: 0%;
    width: 45%;
    z-index: 2;
  }
  .popup-unit-panel {
    position: absolute;
    left: 0%;
    width: 100%;
    z-index: 1;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .popup-background {
    position: "absolute";
    left: "0%";
    top: "0%";
    width: "100%";
    height: "100%";
    opacity: "0.7";
    background-color: "#000";
  }
  .popup-simple {
    position: "absolute";
    left: "30%";
    top: "30%";
    width: "30%";
    height: "30%";
  }
  .popup-unit {
    position: absolute;
    right: 0%;
    width: 45%;
    z-index: 2;
  }
  .popup-unit-panel {
    position: absolute;
    left: 0%;
    width: 100%;
    z-index: 1;
  }
}
.popup-close {
  position: absolute;
  top: 15px;
  left: 10px;
  font-weight: 900;
}

.square {
  padding: 0;
  border-radius: 0;
}
